<template>
  <div id="mine">
    <v-picture :picSrc="userInfo.picture" class="user-picture"></v-picture>
    <div class="user-name">张大亨</div>
  </div>
</template>

<script>
  export default {
    name: 'mine',
    data () {
      return {
        userInfo: {
          picture: 'a072e5a7-cdb7-481d-92ed-1493a936bef8'
        }
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
#mine {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #F5F5F5;
  .user-picture {
    width: 40%;
    border-radius: 50%;
  }
  .user-name {
    margin-top: 20px;
    font-size: 20px;
  }
}
</style>



